/* movie-row.component.css 그대로 복사 */
.wishlist-indicator {
    position: absolute;
    top: 5px;
    right: 5px;
    background-color: rgba(0, 0, 0, 0.7);
    color: white;
    padding: 5px;
    border-radius: 50%;
    font-size: 12px;
  }
  
  .movie-row {
    margin-bottom: 40px;
    position: relative;
    width: 100%;
    overflow: hidden;
  }
  
  .movie-row h2 {
    text-align: left;
    margin-left: 30px;
  }
  
  .slider-container {
    position: relative;
    touch-action: pan-y;
  }
  
  .slider-window {
    overflow: hidden;
    margin: 0 40px;
  }
  
  .movie-slider {
    display: flex;
    transition: transform 0.3s ease;
    padding: 20px 0;
  }
  
  .movie-card {
    flex: 0 0 auto;
    width: 200px;
    margin-right: 10px;
    transition: transform 0.3s;
    position: relative;
    cursor: pointer;
  }
  
  .movie-card:hover {
    transform: scale(1.05);
  }
  
  .movie-card img {
    width: 100%;
    height: auto;
    border-radius: 4px;
  }
  
  .slider-button {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    background-color: rgba(0, 0, 0, 0.5);
    color: white;
    border: none;
    padding: 20px 10px;
    cursor: pointer;
    z-index: 10;
    transition: opacity 0.3s, background-color 0.3s;
  }
  
  .slider-button:hover {
    background-color: rgba(0, 0, 0, 0.8);
  }
  
  .slider-button.left {
    left: 0;
  }
  
  .slider-button.right {
    right: 0;
  }
  
  .slider-button:disabled {
    opacity: 0.5;
    cursor: not-allowed;
  }
  
  @media (max-width: 768px) {
    .movie-row {
      margin-bottom: 20px;
    }
  
    .movie-card {
      width: 120px;
      margin-right: 5px;
    }
  
    .movie-slider {
      padding: 0;
    }
  
    .slider-window {
      margin: 0 10px;
    }
  }
  