.wishlist-indicator {
    position: absolute;
    top: 0;
    right: 10px;
    font-size: 20px;
    background-color: rgba(229, 9, 20, 0.5);
    box-shadow: 0 0 5px rgba(229, 9, 20, 0.7);
  }
  
  .movie-grid {
    width: 100%;
    height: calc(100vh - 200px);
    margin-bottom: 40px;
    margin-top: 30px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  
  .grid-container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .movie-row {
    display: flex;
    justify-content: center;
    margin: 0 auto 20px;
    width: 100%;
  }
  
  .grid-container.list .movie-row {
    flex-direction: column;
  }
  
  .movie-card {
    width: 200px;
    margin: 0 10px;
    transition: transform 0.3s;
    position: relative;
  }
  
  .grid-container.list .movie-card {
    width: 100%;
    display: flex;
    align-items: center;
    margin-bottom: 10px;
  }
  
  .movie-card:hover {
    transform: scale(1.05);
  }
  
  .movie-card img {
    width: 80%;
    aspect-ratio: 1/1;
    border-radius: 4px;
    object-fit: cover;
  }
  
  .grid-container.list .movie-card img {
    width: 100px;
    margin-right: 20px;
  }
  
  .movie-title {
    margin-top: 5px;
    text-align: center;
    font-size: 14px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  
  .grid-container.list .movie-title {
    text-align: left;
    white-space: normal;
  }
  
  .pagination {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
  }
  
  .pagination button {
    background-color: #333;
    color: white;
    border: none;
    padding: 10px 15px;
    margin: 0 10px;
    cursor: pointer;
    border-radius: 4px;
  }
  
  .pagination button:disabled {
    opacity: 0.5;
    cursor: not-allowed;
  }
  
  @media (max-width: 768px) {
    .movie-grid {
      height: calc(90svh - 200px);
    }
  
    .movie-card {
      width: 90px;
      margin: 0 5px;
    }
  
    .movie-title {
      font-size: 12px;
    }
  
    .pagination button {
      padding: 8px 12px;
      font-size: 14px;
    }
  
    .grid-container.list .movie-card img {
      width: 60px;
    }
  }
  